import React from 'react';
import Resume from '../assets/images/ee.png';
import { Link } from 'react-router-dom';

const Homepage = () => {
  return (
    <div>
      <header className="masthead">
          <div className="container px-5">
              <div className="row gx-5 align-items-center">
                  <div className="col-lg-6">

                      <div className="mb-5 mb-lg-0 text-center text-lg-start">
                          <h1 className="display-1 lh-1 mb-3">Create your Resume with Resume Builder.</h1>
                          <p className="lead fw-normal text-muted mb-5">Start creating your Resume with the existing custom Resume Templates</p>
                          <Link
                            className="btn btn-outline-dark py-3 px-4 rounded-pill" 
                            to="/login"
                          >
                            Create Resume for free
                          </Link>
                      </div>
                  </div>
                  <div className="col-lg-6">
                      <div className="masthead-device-mockup">
                        <img className="homepage-right-image" src={Resume} alt="" />
                      </div>
                  </div>
              </div>
          </div>
      </header>
      <aside className="text-center bg-gradient-primary-to-secondary">
          <div className="container px-5">
              <div className="row gx-5 justify-content-center">
                  <div className="col-xl-8">
                      <div className="h2 fs-1 text-white mb-4">"A creative way to build your Resume with Resume Builder"</div>
                  </div>
              </div>
          </div>
      </aside>
    </div>
  )
}

export default Homepage;